var express = require('express'),
	request = require('request'),
	crypt = require('../kripto.js'),
	router = express.Router(),
	db = require('../db.js'),
	jwt = require('./jwt'),
	TYPES = require('tedious').TYPES,
	kripto = require('../kripto'),
	fs = require('fs');

const ActiveDirectory = require('activedirectory2');

const axios = require('axios').default;

const { first } = require('rxjs');
const { uuid } = require('uuidv4');
const { sendRegistrationMail, sendPassResetEmail } = require('../mail/mail');
const { handleRequestUser, decryptIfEncrypted, makeObservableConnection, groupFlatProperies } = require('../services/app.service.js');
const { createUpsertOsobniPodaciRequest } = require('../services/profile.service.js');

function fetchUserLogin(user, res) {
	return new Promise((resolve) => {
		const conn = db.createConnection(res.locals.currDatabase);
		const request = db.createRequest('[Sigurnost].[spApplicationUserLogin_select]', conn, res);
		user.LoginName && request.addParameter('LoginName', TYPES.NVarChar, user.LoginName);
		user.LDAPLoginName && request.addParameter('LDAPLoginName', TYPES.NVarChar, user.LDAPLoginName);

		db.execStoredProcFromNode(request, conn, res, (output, outputParams, dbUser) => {
			const adminUser = dbUser.find((u) => u.IsAdmin);
			if (adminUser) {
				resolve(adminUser);
			} else {
				resolve(dbUser[0]);
			}
		});
	});
}
function fetchUserByPk(PkUsera, res) {
	return new Promise((resolve) => {
		const conn = db.createConnection(res.locals.currDatabase);
		const request = db.createRequest('[Sigurnost].[spApplicationUser_select_NO_JSON]', conn, res);
		request.addParameter('PkUsera', TYPES.Int, PkUsera);

		db.execStoredProcFromNode(request, conn, res, (output, outputParams, dbUser) => {
			resolve(dbUser[0]);
		});
	});
}

function findUserByUUID(UUID, res) {
	return new Promise((resolve) => {
		const conn = db.createConnection(res.locals.currDatabase);
		const request = db.createRequest('[Sigurnost].[spApplicationUserUUID_select]', conn, res);
		request.addParameter('UUID', TYPES.NVarChar, UUID);
		db.execStoredProcFromNode(request, conn, res, (output, outputParams, dbUser) => {
			resolve(dbUser[0]);
		});
	});
}

/**
 *
 * @param {{status:0|1|2|3, user:any, res:any}} , REGISTRIRAN = 0, AKTIVAN = 1, ZABORAVIO_PASS = 2, ADMIN_ISKLJUCIO = 3,
 */
function setUserAccountActivityStatus({ status, user, res }) {
	return new Promise((resolve) => {
		const conn = db.createConnection(res.locals.currDatabase);
		const request = db.createRequest('[Sigurnost].[spApplicationUserStatusRacuna_Update]', conn, res);
		request.addParameter('KorisnikPkUsera', TYPES.Int, crypt.decryptString(user.PkUsera));
		request.addParameter('StatusKorisnika', TYPES.Int, status);
		request.addParameter('AktivanDaNe', TYPES.Int, user.UserAktivanDaNe);
		request.addParameter('RowVersion', TYPES.NVarChar, user.RowVersion);
		db.execStoredProcFromNode(request, conn, res, (output, outputParams, dbResult) => {
			resolve(dbResult[0]);
		});
	});
}

/**
 *
 * @param {status:0|1|2|3} StatusKorisnika , REGISTRIRAN = 0, AKTIVAN = 1, ZABORAVIO_PASS = 2, ADMIN_ISKLJUCIO = 3,
 */
function checkKorisnikStatusCanLogin(StatusKorisnika) {
	switch (true) {
		case StatusKorisnika == 0:
			return { rejectLogin: true, message: 'STD:INACTIVE_USER' };
		case StatusKorisnika == 1:
			return { rejectLogin: false };
		case StatusKorisnika == 2:
			return { rejectLogin: true, message: 'STD:PASS_RESET_CHECK_MAIL' };
		case StatusKorisnika == 3:
			return { rejectLogin: true, message: 'STD:BANNED_ACC' };
		default:
			return { rejectLogin: true, message: 'STD:INACTIVE_USER' };
	}
}

function syncWithEduplan({ authHeader, OIB }) {
	return new Promise(async (resolve) => {
		const token = jwt.extractJwt(authHeader);
		const { API_URL } = global.appConfig.eduplanSync;
		try {
			const user = await axios.get(API_URL, { headers: { authorization: `Bearer ${token}` }, params: { OIB } });
			const data = (user && user.data) || null;
			resolve(data ? groupFlatProperies(data.user) : {});
		} catch (error) {
			global.appConfig.ukljuciConsoleLog.ukljuciStackTrace && console.trace(error);
			return resolve({});
		}
	});
}

function syncEduplanPayload({ osobniPodaci, eduplanUser, PkUsera }) {
	const { meta, nastavnik, student } = eduplanUser;
	const dataFeed = { ...meta, ...(nastavnik && nastavnik.PkNastavnikSuradnik ? nastavnik : student) };
	const payload = {
		...osobniPodaci,
		Spol: dataFeed.Spol ? (dataFeed.Spol.toLowerCase().startsWith('Mu') ? 'MUSKO' : 'ZENSKO') : null,
		ImeUsera: dataFeed.Ime,
		PrezimeUsera: dataFeed.Prezime,
		DatumRodenja: dataFeed.DatumRodjenja || dataFeed.DatumRodenja,
		JMBAG: dataFeed.JMBAG || null,
		Email: dataFeed.Email || dataFeed.Email1 || dataFeed.Email2,
		PkDrzava: dataFeed.PkDrzava,
		Grad: `${dataFeed.NazivGrada || ''} ${dataFeed.PostanskiBroj || ''} ${dataFeed.PostanskiBrojPA || ''}`,
		Adresa: dataFeed.Adresa || dataFeed.AdresaKucna,
		Mobitel: dataFeed.BrojMobitela || dataFeed.Mobitel
	};
	return {
		body: {
			...payload,
			PkUsera,
			PkOsobniPodaciPkUsera: PkUsera
		}
	};
}

const login = {
	handleLoginPermit: (dbUser, reject) => {
		if (dbUser.UserAktivanDaNe == 0) {
			return reject({ message: 'STD:INACTIVE_USER' });
		}
		const status = checkKorisnikStatusCanLogin(dbUser.StatusKorisnika);
		if (status.rejectLogin) {
			return reject({ message: status.message });
		}
		return null;
	},

	registerInWithLDAP: function ({ LoginName, Lozinka }, res) {
		return new Promise((resolve, reject) => {
			const client = new ActiveDirectory({
				url: global.appConfig.ldap.ActiveDirectoryApi,
				username: LoginName,
				password: Lozinka,
				baseDN: global.appConfig.ldap.baseDN
			});
			client.authenticate(LoginName, Lozinka || 'wrong_pass_if_empty', async (err, auth) => {
				if (err) return reject({ message: 'STD:INVALID_CREDENTIALS' });

				client.findUser(LoginName, async (err, user) => {
					if (err) return reject({ message: 'STD:INVALID_CREDENTIALS' });

					const { sn, givenName, userPrincipalName, sAMAccountName } = user;
					const insertedUser = await insertUser(
						{
							PrezimeUsera: sn || '-',
							ImeUsera: givenName || '-',
							LDAPLoginName: sAMAccountName,
							LoginName: sAMAccountName,
							Lozinka: uuid(),
							ObaveznaIzmjenaLozinkeDaNe: 0,
							Email: userPrincipalName,
							UserAktivanDaNe: 1,
							StatusKorisnika: 1
						},
						res,
						false
					);
					resolve(jwt.loginJwt({ ...insertedUser, ...{ currDatabase: res.locals.currDatabase } }));
				});
			});
		});
	},
	signInWithLDAP: function ({ LoginName, Lozinka }, res) {
		return new Promise((resolve, reject) => {
			const client = new ActiveDirectory({
				url: global.appConfig.ldap.ActiveDirectoryApi
			});
			client.authenticate(LoginName, Lozinka || 'wrong_pass_if_empty', async (err, auth) => {
				console.log(err, auth);
				if (err) {
					reject({ message: 'STD:INVALID_CREDENTIALS' });
				} else {
					let dbUser = null;
					try {
						const credential = LoginName.split(global.appConfig.ldap.ldapDomena)[0];
						dbUser = await fetchUserLogin({ LDAPLoginName: credential }, res);
					} catch (error) {
						reject({ message: 'STD:INVALID_CREDENTIALS' });
					}
					if (dbUser) {
						this.handleLoginPermit(dbUser, reject);
						resolve(jwt.loginJwt({ ...dbUser, ...{ currDatabase: res.locals.currDatabase } }));
					}
					reject({ message: 'STD:INVALID_CREDENTIALS' });
				}
			});
		});
	},
	/**
	 *
	 * @param {any} req
	 * @returns {Promise<Object|string>}  {error: string} | token: string
	 */
	userLogin: function (req, res, user) {
		return new Promise(async (resolve, reject) => {
			let dbUser = null;
			try {
				dbUser = await fetchUserLogin(user, res);
			} catch (error) {
				reject({ message: 'STD:INVALID_CREDENTIALS' });
			}
			if (dbUser) {
				this.handleLoginPermit(dbUser, reject);
				if (user.LoginName === kripto.decryptAdminPassword(user.Lozinka, false)) {
					//TODO DB login dobar?
					resolve(jwt.loginJwt({ ...dbUser, ...{ currDatabase: res.locals.currDatabase } }));
				} else if (user.Lozinka == kripto.decryptString(dbUser.Lozinka)) {
					resolve(jwt.loginJwt({ ...dbUser, ...{ currDatabase: res.locals.currDatabase } }));
				}
			}
			reject({ message: 'STD:INVALID_CREDENTIALS' });
		});
	},
	aaiLogin: function (req, res, user) {
		return new Promise(async (resolve, reject) => {
			const { givenName, hrEduPersonUniqueID, sn, hrEduPersonOIB } = user;
			let dbUser = null;
			try {
				dbUser = await fetchUserLogin({ LoginName: hrEduPersonUniqueID }, res);
			} catch (error) {
				reject({ message: 'STD:INVALID_REQUEST' });
			}
			const findOsobniPodaciForUser = (PkUsera) => {
				const conn = db.createConnection(res.locals.currDatabase);
				const request = db.createRequest('[Alumni].[spOsobniPodaci_select]', conn, res);
				request.addParameter('PkUsera', TYPES.Int, decryptIfEncrypted(PkUsera));
				return makeObservableConnection(request, conn, res, db);
			};

			if (dbUser) {
				const finishAAIUser = () => {
					this.handleLoginPermit(dbUser, reject);
					resolve(jwt.loginJwt({ ...dbUser, ...{ currDatabase: res.locals.currDatabase } }));
				};
				const needsUpdate = (param) => (param ? (param == '-' ? true : false) : true);

				if (needsUpdate(dbUser.ImeUsera) || needsUpdate(dbUser.PrezimeUsera) || needsUpdate(dbUser.OIB)) {
					const updateOsobniPodaci = (userMeta) => {
						const { ImeUsera, PrezimeUsera, OIB } = userMeta;

						const conn = db.createConnection(res.locals.currDatabase);
						const request = db.createRequest('[Alumni].[spOsobniPodaci_update]', conn, res);
						request.addParameter('PkUsera', TYPES.Int, decryptIfEncrypted(dbUser.PkUsera));
						request.addParameter('ImeUsera', TYPES.NVarChar, needsUpdate(ImeUsera) ? givenName : ImeUsera);
						request.addParameter('PrezimeUsera', TYPES.NVarChar, needsUpdate(PrezimeUsera) ? sn : PrezimeUsera);
						request.addParameter('OIB', TYPES.NVarChar, needsUpdate(OIB) ? hrEduPersonOIB : OIB);

						return makeObservableConnection(request, conn, res, db);
					};

					return findOsobniPodaciForUser(dbUser.PkUsera)
						.pipe(first())
						.subscribe(([userMeta]) =>
							updateOsobniPodaci(userMeta)
								.pipe(first())
								.subscribe((result) => {
									finishAAIUser();
								})
						);
				} else {
					finishAAIUser();
				}
			} else {
				try {
					const insertedUser = await insertUser(
						{
							PrezimeUsera: sn || '-',
							ImeUsera: givenName || '-',
							LDAPLoginName: null,
							LoginName: hrEduPersonUniqueID,
							Lozinka: uuid(),
							ObaveznaIzmjenaLozinkeDaNe: 0,
							Email: null,
							UserAktivanDaNe: 1,
							StatusKorisnika: 1
						},
						res,
						false,
						{ OIB: hrEduPersonOIB || '-' }
					);
					if (hrEduPersonOIB) {
						const eduplanUser = await syncWithEduplan({ authHeader: `Bearer ${jwt.loginJwt({ OIB: hrEduPersonOIB })}`, OIB: hrEduPersonOIB });
						if (eduplanUser && eduplanUser.meta && eduplanUser.meta.PkUsera) {
							return findOsobniPodaciForUser(insertedUser.PkUsera)
								.pipe(first())
								.subscribe(([userMeta]) => {
									const osobniPodaciPayload = syncEduplanPayload({ osobniPodaci: userMeta, eduplanUser, PkUsera: decryptIfEncrypted(insertedUser.PkUsera) });
									const { request, conn } = createUpsertOsobniPodaciRequest(osobniPodaciPayload, res);
									makeObservableConnection(request, conn, res, db)
										.pipe(first())
										.subscribe({
											next: async (finished) => {
												const syncedUser = await fetchUserLogin({ LoginName: hrEduPersonUniqueID }, res);
												resolve(jwt.loginJwt({ ...syncedUser, ...{ currDatabase: res.locals.currDatabase } }));

												resolve(jwt.loginJwt({ ...insertedUser, ...{ currDatabase: res.locals.currDatabase } }));
											},
											error: (err) => {
												reject({ message: 'STD:INVALID_REQUEST' });
											}
										});
								});
						} else {
							resolve(jwt.loginJwt({ ...insertedUser, ...{ currDatabase: res.locals.currDatabase } }));
						}
					} else {
						resolve(jwt.loginJwt({ ...insertedUser, ...{ currDatabase: res.locals.currDatabase } }));
					}
				} catch (error) {
					reject({ message: 'STD:INVALID_REQUEST' });
				}
			}
		});
	}
};

router.get('/test', function (req, res) {
	res.send({ message: 'OK' });
});
router.get('/test/auth', function (req, res) {
	res.send(req.user);
});

router.put('/syncEduplan', async function (req, res) {
	const { osobniPodaci, OIB, PkUsera } = req.body;
	const eduplanUser = await syncWithEduplan({ authHeader: req.headers.authorization, OIB });
	const osobniPodaciPayload = syncEduplanPayload({ osobniPodaci, eduplanUser, PkUsera });
	const { request, conn } = createUpsertOsobniPodaciRequest(osobniPodaciPayload, res);
	makeObservableConnection(request, conn, res, db)
		.pipe(first())
		.subscribe({
			next: (finished) => {
				res.send(osobniPodaciPayload);
			},
			error: (err) => {
				res.send(err);
			}
		});
});

router.put('/refreshJWT', async function (req, res) {
	const { token } = req.body;
	const { payload } = jwt.decodeToken(token) || {};
	if (payload) {
		const { LoginName } = payload;
		const dbUser = await fetchUserLogin({ LoginName }, res);
		if (dbUser) {
			res.send({ token: jwt.loginJwt({ ...dbUser, ...{ currDatabase: res.locals.currDatabase } }) });
		}
	} else {
		res.send({});
	}
});

router.post('/ldapLogin', async function (req, res) {
	if (req.body && req.body.user) {
		const { LoginName, Lozinka } = req.body.user;
		try {
			const token = await login.signInWithLDAP({ LoginName: LoginName + global.appConfig.ldap.ldapDomena, Lozinka }, res);
			if (token) {
				res.send({ token });
			}
		} catch (error) {
			// acc doesnt exist
			global.appConfig.ukljuciConsoleLog.ukljuciStackTrace && console.trace(error);
			try {
				const token = await login.registerInWithLDAP({ LoginName: LoginName + global.appConfig.ldap.ldapDomena, Lozinka }, res);
				if (token) {
					res.send({ token });
				}
			} catch (error) {
				global.appConfig.ukljuciConsoleLog.ukljuciStackTrace && console.trace(error);
			}
		}
	}
});

router.post('/login', async function (req, res) {
	if (req.body && req.body.user) {
		const { LoginName, Lozinka } = req.body.user;
		if (LoginName.endsWith(global.appConfig.ldap.ldapDomena)) {
			try {
				const token = await login.signInWithLDAP({ LoginName, Lozinka }, res);
				if (token) {
					res.send({ token });
				}
			} catch (error) {
				global.appConfig.ukljuciConsoleLog.ukljuciStackTrace && console.trace(error);
			}
		}
		login
			.userLogin(req, res, req.body.user)
			.then((token) => {
				res.send({ token: token });
			})
			.catch((err) => {
				res.status(401).send(err);
			});
	} else {
		res.status(401).send({ message: 'STD:INVALID_REQUEST' });
	}
});

router.post('/aaiLogin', function (req, res) {
	if (req.body) {
		const { givenName, hrEduPersonUniqueID, sn, hrEduPersonOIB } = req.body;
		login
			.aaiLogin(req, res, { givenName, hrEduPersonUniqueID, sn, hrEduPersonOIB })
			.then((token) => {
				res.send({ token: token });
			})
			.catch((err) => {
				res.status(401).send(err);
			});
	} else {
		res.status(401).send({ message: 'STD:INVALID_REQUEST' });
	}
});

