const { mailInit, constructEmailOptions, sendMail, translate } = require('../mailService');
const path = require('path');

async function sendAnketaLinkMail({ user, lang, PkAnketa }) {
	const { anketaLink } = global.appConfig.mailConfig.emails;
	const { mailConfig } = global.appConfig;

	const mailTransport = mailInit();
	const options = constructEmailOptions({
		lang: lang,
		dest: {
			to: user.Email,
			subject: translate(lang, 'ANKETA_LINK')
		},
		templateParams: {
			ANKETA_LINK: translate(lang, 'ANKETA_LINK'),
			ANKETA_LINK_HEADER: translate(lang, 'ANKETA_LINK_HEADER'),
			POSTOVANI: translate(lang, 'POSTOVANI'),
			ime: user.ImeUsera,
			prezime: user.PrezimeUsera,
			ANKETA_LINK_TEXT_0: translate(lang, 'ANKETA_LINK_TEXT_0'),
			link: `${anketaLink.url}/${PkAnketa}`,
			ANKETA_BUTTON_LABEL: translate(lang, 'ANKETA_BUTTON_LABEL'),
			ORG_NEED_HELP: translate(lang, 'ORG_NEED_HELP'),
			ACC_CONFIRM_BACKUP_LINK: translate(lang, 'ACC_CONFIRM_BACKUP_LINK'),
			organizationContactLink: mailConfig.incomingMailParams.organizationContactLink,
			staticUrl: mailConfig.incomingMailParams.staticUrl,
			ORGANIZATION: translate(lang, 'ORGANIZATION'),
			ADDRESS: translate(lang, 'ADDRESS'),
			appLink: mailConfig.incomingMailParams.appLink
		},
		templateUrl: path.join(anketaLink.template),
		attachments: anketaLink.attachments
	});
	return await sendMail({ mailTransport, options });
}

module.exports = { sendAnketaLinkMail };
