const express = require('express');
const router = express.Router();
const { TYPES } = require('tedious');
const db = require('../db.js');
const { simpleDbResolve, handleRequestUser, exists, constructConnection, makeObservableConnection, makeObservableConnectionWithOutput, groupFlatProperies, flatten, decryptIfEncrypted, stringToSentence, hasClaim } = require('../services/app.service');
const { forkJoin, from, first, mergeMap, combineLatest, take, concat, last, of } = require('rxjs');
const { sendAnketaLinkMail } = require('../mail/mail.js');
const { tryDecryptOrReturnInput } = require('../kripto.js');
const { insertPredlozakWithOutput, updatePredlozakWithOutput, insertPitanjePredlozak, groupStats, fetchAnketaStatistika, fetchAnketaStatistikaPitanja } = require('../services/ankete.service.js');

const modulName = 'Ankete';

//#region  GET

router.get('/predlosci', function (req, res) {
	const conn = db.createConnection(res.locals.currDatabase);
	const request = db.createRequest('Ankete.spPredlozak_select', conn, res);
	exists(req.query.PkPredlozak) && request.addParameter('PkPredlozak', TYPES.Int, req.query.PkPredlozak);
	exists(req.query.PkUsera) && request.addParameter('PkUsera', TYPES.Int, req.query.PkUsera);
	exists(req.query.skip) && request.addParameter('skip', TYPES.Int, req.query.skip);
	exists(req.query.take) && request.addParameter('take', TYPES.Int, req.query.take);

	db.execStoredProcFromNode(request, conn, res, (output, outputParams, result) => simpleDbResolve(output, outputParams, result, res));
});

router.get('/ankete', async function (req, res) {
	const conn = db.createConnection(res.locals.currDatabase);
	const request = db.createRequest('Ankete.spAnketa_select', conn, res);
	exists(req.query.PkAnketa) && request.addParameter('PkAnketa', TYPES.Int, req.query.PkAnketa);
	exists(req.query.PkUsera) && request.addParameter('PkUsera', TYPES.Int, req.query.PkUsera);
	exists(req.query.skip) && request.addParameter('skip', TYPES.Int, req.query.skip);
	exists(req.query.take) && request.addParameter('take', TYPES.Int, req.query.take);
	exists(req.query.IsPublic) && request.addParameter('IsPublic', TYPES.Int, +req.query.IsPublic);

	db.execStoredProcFromNode(request, conn, res, (output, outputParams, result) => {
		if (output == 'OK') {
			return res.status(200).send(result.map((r) => groupFlatProperies(r)));
		}
		return res.status(500).send({ message: 'STD:INVALID_REQUEST' });
	});
});

router.get('/predlozak/templating', function (req, res) {
	const { PkPredlozak } = req.query;

	const fetchTipoviPitanja = ({ predlozak = null, pitanja = null }) => {
		const conn = db.createConnection(res.locals.currDatabase);
		const request = db.createRequest('AP.spPitanjeTip_select', conn, res);
		return db.execStoredProcFromNode(request, conn, res, (output, outputParams, result) => {
			if (output == 'OK') {
				res.status(200).send({
					predlozak: predlozak,
					pitanja: pitanja,
					tipoviPitanja: result
				});
			} else {
				res.status(500).send({ message: 'STD:INVALID_REQUEST' });
			}
		});
	};

	// ovo znaci da je blank slate
	if (!exists(PkPredlozak)) {
		return fetchTipoviPitanja({});
	}

	const connections$ = ['Ankete.spPredlozak_select', 'Ankete.spPitanjaZaPredlozak_select'].map((proc) => {
		const { request, conn } = constructConnection(proc, res.locals.currDatabase, res, db);
		request.addParameter('PkPredlozak', TYPES.Int, PkPredlozak);
		return from(
			new Promise((resolve) => {
				db.execStoredProcFromNode(request, conn, res, (output, outputParams, result) => {
					if (output == 'OK') {
						resolve(result);
					}
					resolve([]);
				});
			})
		);
	});

	const obs = forkJoin(connections$).subscribe({
		next: ([[predlozak], pitanja]) => {
			if (!predlozak) {
				return res.status(500).send({ message: 'STD:INVALID_REQUEST' });
			}
			if (!pitanja.length) {
				return fetchTipoviPitanja({
					predlozak,
					pitanja: []
				});
			}
			const connectionsOdgovori$ = pitanja.map((p) => {
				const { PkPitanje } = p;
				const conn = db.createConnection(res.locals.currDatabase);
				const request = db.createRequest('Ankete.spOdgovorZaPitanje_select', conn, res);
				request.addParameter('PkPitanje', TYPES.Int, PkPitanje);

				return from(
					new Promise((resolve) => {
						db.execStoredProcFromNode(request, conn, res, (output, outputParams, result) => {
							if (output == 'OK') {
								resolve(result);
							}
							resolve([]);
						});
					})
				);
			});
			const obsOdg = forkJoin(connectionsOdgovori$).subscribe({
				next: (odgovoriMap) => {
					const trimAndConvertInt = (value) => Number(value.trim());

					const findRelevantQuestion = (odgovoriMap, PkPitanje) => {
						for (const odgovori of odgovoriMap) {
							const odgovoriZaPitanje = odgovori.find((i) => i.PkPitanje === PkPitanje);
							if (odgovoriZaPitanje) {
								return odgovori.map((o) => ({ ...o, ...{ OdgovorRedoslijed: trimAndConvertInt(o.OdgovorRedoslijed) } }));
							}
						}
					};
					return fetchTipoviPitanja({
						predlozak,
						pitanja: pitanja.map((p) => ({ ...p, ...{ PitanjeRedoslijed: trimAndConvertInt(p.PitanjeRedoslijed) }, odgovori: findRelevantQuestion(odgovoriMap, p.PkPitanje) }))
					});
				},
				error: (err) => {
					res.status(500).send({ ...err, message: 'STD:INVALID_REQUEST' });
				},
				complete: () => {
					setTimeout(() => {
						obsOdg.unsubscribe();
					});
				}
			});
		},
		error: (err) => {
			res.status(500).send({ ...err, message: 'STD:INVALID_REQUEST' });
		},
		complete: () => {
			setTimeout(() => {
				obs.unsubscribe();
			});
		}
	});
});

router.get('/anketa/solved/check', async (req, res) => {
	const user = await handleRequestUser(req, res);
	const { PkAnketa } = req.query;
	const fetchAnketaUser = ({ PkAnketa, PkUser }) => {
		const { request, conn } = constructConnection('Ankete.spAnketaUser_select', res.locals.currDatabase, res, db);
		request.addParameter('PkAnketa', TYPES.Int, PkAnketa);
		request.addParameter('PkUser', TYPES.Int, PkUser);
		return makeObservableConnection(request, conn, res, db);
	};
	return fetchAnketaUser({ PkAnketa, PkUser: user.PkUsera })
		.pipe(first())
		.subscribe({
			next: ([anketaWithUser]) => {
				if (anketaWithUser) {
					res.status(200).send({ isSolved: !!anketaWithUser.UserPristupioAnketiDaNe, eligible: true });
				} else {
					res.status(200).send({ isSolved: false, eligible: false });
				}
			},
			error: (err) => {
				res.status(200).send(err);
			}
		});
});

router.get('/anketa/statistika', async function (req, res) {
	const { PkAnketa, PkPredlozak } = req.query;
	const params = {
		...(PkAnketa && { PkAnketa }),
		...(PkPredlozak && { PkPredlozak })
	};
	forkJoin([fetchAnketaStatistika({ ...params, res, db }), fetchAnketaStatistikaPitanja({ ...params, res, db })])
		.pipe(first())
		.subscribe(([[statsAnketa], statsPitanja]) => {
			const groupedStatsPitanja = groupStats(statsPitanja);
			res.send({
				anketaStats: statsAnketa,
				pitanjaStats: groupedStatsPitanja
			});
		});
});

