router.post('/predlozak/copy', async function (req, res) {
	const hasAcces = await hasClaim({ modul: modulName, claim: 'Predlozak' }, req);
	if (!hasAcces) {
		return res.status(401).send({ message: 'STD:UNATHORIZED_ACCES' });
	}

	const user = await handleRequestUser(req, res);
	const { predlozak: metadata } = req.body;
	const { predlozak, pitanja, tipoviPitanja } = metadata;
	insertPredlozakWithOutput({ predlozak, db, res, user })
		.pipe(first())
		.subscribe(({ PkPredlozak }) => {
			forkJoin(pitanja.map((pitanje) => insertPitanjePredlozak({ PkPitanje: pitanje.PkPitanje, PkPredlozak, db, res, user, pitanje })))
				.pipe(first())
				.subscribe((questionsInserted) => {
					res.send({ message: 'OK' });
				});
		});
});

router.post('/predlozak/templating', async function (req, res) {
	const hasAcces = await hasClaim({ modul: modulName, claim: 'Predlozak' }, req);
	if (!hasAcces) {
		return res.status(401).send({ message: 'STD:UNATHORIZED_ACCES' });
	}
	const user = await handleRequestUser(req, res);
	const { predlozak } = req.body;
	const { PkPredlozak } = predlozak;
	if (PkPredlozak) {
		return updatePredlozakWithOutput({ predlozak, PkPredlozak, db, user, res })
			.pipe(first())
			.subscribe(({ PkPredlozak }) => {
				res.status(200).send({ PkPredlozak });
			});
	} else {
		return insertPredlozakWithOutput({ predlozak, db, res, user })
			.pipe(first())
			.subscribe(({ PkPredlozak }) => {
				res.status(200).send({ PkPredlozak });
			});
	}
});

router.post('/anketa/solved', async function (req, res) {
	const user = await handleRequestUser(req, res);
	const { pitanja, PkAnketa, PkPredlozak, anketa } = req.body;

	const fetchAnketaUser = ({ PkAnketa, PkUser }) => {
		const { request, conn } = constructConnection('Ankete.spAnketaUser_select', res.locals.currDatabase, res, db);
		request.addParameter('PkAnketa', TYPES.Int, PkAnketa);
		request.addParameter('PkUser', TYPES.Int, PkUser);
		return makeObservableConnection(request, conn, res, db);
	};
	const setAnketaIsSolved = ({ PkAnketa, PkUser }) => {
		const { request, conn } = constructConnection('Ankete.spAnketaUserSolved_update', res.locals.currDatabase, res, db);
		request.addParameter('PkAnketa', TYPES.Int, PkAnketa);
		request.addParameter('PkUser', TYPES.Int, PkUser);
		return makeObservableConnection(request, conn, res, db);
	};

	const insertAnswerToQuestion = ({ PkAnketaUserPitanje, Odgovor }) => {
		const { request, conn } = constructConnection('Ankete.spAnketaUserSolvedQuestionOdgovor_insert', res.locals.currDatabase, res, db);
		request.addParameter('PkAnketaUserPitanje', TYPES.Int, PkAnketaUserPitanje);
		if (Odgovor) {
			const { PkOdgovor, OdgovorTekst } = Odgovor;
			PkOdgovor && request.addParameter('PkOdgovor', TYPES.Int, PkOdgovor);
			request.addParameter('UserOdgovorTekst', TYPES.NVarChar, OdgovorTekst);
		}
		request.addOutputParameter('PkAnketaUserPitanjeOdgovor', TYPES.Int, null);
		return makeObservableConnection(request, conn, res, db);
	};

	const setStatisticAnketa = () => {
		const { request, conn } = constructConnection('Ankete.[spStatistikaAnketaCounter_upsert]', res.locals.currDatabase, res, db);
		request.addParameter('PkAnketa', TYPES.Int, PkAnketa);
		request.addParameter('PkPredlozak', TYPES.Int, PkPredlozak);
		request.addOutputParameter('PkStatistikaAnketa', TYPES.Int, null);
		return makeObservableConnectionWithOutput(request, conn, res, db, 'PkStatistikaAnketa');
	};

	const setQuestionStatistic = ({ PkPitanje, PkOdgovor, OdgovorTekst }) => {
		const { request, conn } = constructConnection('Ankete.[spStatistikaAnketaPitanjaOdgovor_upsert]', res.locals.currDatabase, res, db);
		request.addParameter('PkAnketa', TYPES.Int, PkAnketa);
		request.addParameter('PkPredlozak', TYPES.Int, PkPredlozak);
		request.addParameter('PkPitanje', TYPES.Int, PkPitanje);
		!exists(PkOdgovor) && request.addParameter('AnonimniOdgovorTekst', TYPES.NVarChar, stringToSentence(OdgovorTekst));
		request.addParameter('PkOdgovor', TYPES.Int, PkOdgovor);
		request.addOutputParameter('PkStatistikaAnketaUserPitanjeOdgovor', TYPES.Int, null);
		return makeObservableConnectionWithOutput(request, conn, res, db, 'PkStatistikaAnketaUserPitanjeOdgovor');
	};

	const mapQuestionToSolvedAnketa = ({ PkAnketaUser, pitanje }) => {
		const { PkPitanje, Odgovor } = pitanje;

		const UserOdgovorioNaPitanjeDaNe = exists(Odgovor) ? 1 : 0;

		if (UserOdgovorioNaPitanjeDaNe) {
			if (Array.isArray(Odgovor)) {
				forkJoin(Odgovor.map((o) => setQuestionStatistic({ PkPitanje, PkOdgovor: o.PkOdgovor, OdgovorTekst: o.OdgovorTekst })))
					.pipe(first())
					.subscribe((result) => {});
			} else {
				const { PkOdgovor, OdgovorTekst } = Odgovor;
				setQuestionStatistic({ PkPitanje, PkOdgovor, OdgovorTekst })
					.pipe(first())
					.subscribe(({ PkStatistikaAnketaUserPitanjeOdgovor }) => {});
			}
		}
		if (PkAnketaUser) {
			const { request, conn } = constructConnection('Ankete.spAnketaUserSolvedQuestion_insert', res.locals.currDatabase, res, db);
			request.addParameter('PkAnketaUser', TYPES.Int, PkAnketaUser);
			request.addParameter('PkPitanje', TYPES.Int, PkPitanje);
			request.addParameter('UserOdgovorioNaPitanjeDaNe', TYPES.Int, UserOdgovorioNaPitanjeDaNe);
			request.addOutputParameter('PkAnketaUserPitanje', TYPES.Int, null);

			return makeObservableConnectionWithOutput(request, conn, res, db, 'PkAnketaUserPitanje').pipe(
				first(),
				mergeMap(({ PkAnketaUserPitanje }) => {
					if (UserOdgovorioNaPitanjeDaNe) {
						if (Array.isArray(Odgovor)) {
							return forkJoin(Odgovor.map((o) => insertAnswerToQuestion({ PkAnketaUserPitanje, Odgovor: o }))).pipe(first());
						} else {
							return insertAnswerToQuestion({ PkAnketaUserPitanje, Odgovor }).pipe(first());
						}
					} else {
						return of(null);
					}
				})
			);
		} else {
			return of(true);
		}
	};

	setStatisticAnketa()
		.pipe(first())
		.subscribe((result) => {});

	const { AnketaAnonimnaDaNe } = anketa;

	if (!(user && user.PkUsera)) {
		if (AnketaAnonimnaDaNe) {
			return forkJoin(pitanja.map((q) => mapQuestionToSolvedAnketa({ PkAnketaUser: null, pitanje: q })))
				.pipe(first())
				.subscribe({
					next: (complete) => {
						res.status(200).send({ complete });
					},
					error: (err) => {
						res.status(500).send({ message: err.message });
					}
				});
		} else {
			res.status(200).send({ message: 'STD:ILLEGAL_STATE' });
		}
	} else {
		return fetchAnketaUser({ PkAnketa, PkUser: user.PkUsera })
			.pipe(first())
			.subscribe({
				next: ([anketaWithUser]) => {
					setAnketaIsSolved({ PkAnketa, PkUser: anketaWithUser.PkUser })
						.pipe(first())
						.subscribe((result) => {});

					return forkJoin(pitanja.map((q) => mapQuestionToSolvedAnketa({ PkAnketaUser: anketaWithUser.PkAnketaUser, pitanje: q })))
						.pipe(first())
						.subscribe({
							next: (complete) => {
								res.status(200).send({ complete });
							},
							error: (err) => {
								res.status(500).send({ message: err.message });
							}
						});
				},
				error: (err) => {
					res.status(500).send({ message: err.message });
				}
			});
	}
});

