router.put('/predlozak/templating/question/reorder', async function (req, res) {
	const hasAcces = await hasClaim({ modul: modulName, claim: 'Predlozak' }, req);
	if (!hasAcces) {
		return res.status(401).send({ message: 'STD:UNATHORIZED_ACCES' });
	}
	const user = await handleRequestUser(req, res);
	const { swap, current, target, PkPredlozak } = req.body;
	const { PkPitanjeFrom, PkPitanjeTo } = swap;
	const { request, conn } = constructConnection('Ankete.spPitanjeRedoslijed_Update', res.locals.currDatabase, res, db);
	request.addParameter('PkPitanjeFrom', TYPES.Int, PkPitanjeFrom);
	request.addParameter('PkPitanjeTo', TYPES.Int, PkPitanjeTo);
	request.addParameter('RedoslijedFrom', TYPES.Int, current);
	request.addParameter('RedoslijedTo', TYPES.Int, target);
	request.addParameter('PkPredlozak', TYPES.Int, PkPredlozak);
	request.addParameter('UserPromjene', TYPES.NVarChar, user.ImePrezimeUsera);
	request.addParameter('PkUserPromjene', TYPES.Int, user.PkUsera);

	db.execStoredProcFromNode(request, conn, res, (output, outputParams, result) => {
		simpleDbResolve(output, outputParams, result, res);
	});
});

router.put('/predlozak/templating/question/replace', async function (req, res) {
	const hasAcces = await hasClaim({ modul: modulName, claim: 'Predlozak' }, req);
	if (!hasAcces) {
		return res.status(401).send({ message: 'STD:UNATHORIZED_ACCES' });
	}
	const user = await handleRequestUser(req, res);

	const { pitanje, odgovori, PkPredlozak } = req.body;

	const updatePitanjeMeta = () => {
		const { request, conn } = constructConnection('Ankete.spPitanje_Update', res.locals.currDatabase, res, db);
		const { PkPitanje, PitanjeTekst, PkPitanjeTip, PitanjeAktivnoDaNe, PitanjeObaveznoDaNe } = pitanje;
		request.addParameter('PkPitanje', TYPES.Int, PkPitanje);
		request.addParameter('PitanjeTekst', TYPES.NVarChar, PitanjeTekst);
		request.addParameter('PkPitanjeTip', TYPES.Int, PkPitanjeTip);
		request.addParameter('PitanjeAktivnoDaNe', TYPES.Int, PitanjeAktivnoDaNe);
		request.addParameter('PitanjeObaveznoDaNe', TYPES.Int, PitanjeObaveznoDaNe);
		request.addParameter('PkUser', TYPES.Int, user.PkUsera);
		request.addParameter('User', TYPES.NVarChar, user.ImePrezimeUsera);
		return makeObservableConnection(request, conn, res, db);
	};
	const purgeAnswer = (PkOdgovor) => {
		const { request, conn } = constructConnection('Ankete.spOdgovor_delete', res.locals.currDatabase, res, db);
		request.addParameter('PkOdgovor', TYPES.Int, PkOdgovor);
		return makeObservableConnection(request, conn, res, db);
	};
	const insertAnswer = (odgovor, index) => {
		const { request, conn } = constructConnection('Ankete.spOdgovor_upsert', res.locals.currDatabase, res, db);
		const { PkPitanje } = pitanje;
		const { OdgovorTekst } = odgovor;
		request.addParameter('PkPitanje', TYPES.Int, PkPitanje);
		request.addParameter('OdgovorTekst', TYPES.NVarChar, OdgovorTekst);
		request.addParameter('OdgovorRedoslijed', TYPES.NVarChar, `${index}`);
		request.addParameter('PkUserUnos', TYPES.Int, user.PkUsera);
		request.addParameter('PkUserPromjena', TYPES.Int, user.PkUsera);
		request.addParameter('PkUsera', TYPES.Int, user.PkUsera);
		request.addParameter('User', TYPES.NVarChar, user.ImePrezimeUsera);
		request.addOutputParameter('PkOdgovor', TYPES.Int, null);
		return makeObservableConnection(request, conn, res, db);
	};

	const applyChanges = () => {
		return forkJoin([updatePitanjeMeta(), ...(pitanje.odgovori ? pitanje.odgovori.map((o) => purgeAnswer(o.PkOdgovor)) : []), ...(odgovori && odgovori.length ? odgovori.map((o, i) => insertAnswer(o, i + 1)) : [])])
			.pipe(first())
			.subscribe((result) => {
				res.send({ message: 'OK' });
			});
	};
	if (pitanje.PkPitanje) {
		return applyChanges();
	} else {
		const insertPitanje = () => {
			const { request, conn } = constructConnection('Ankete.spPitanje_Insert', res.locals.currDatabase, res, db);
			const { PitanjeTekst, PkPitanjeTip, PitanjeAktivnoDaNe, PitanjeObaveznoDaNe } = pitanje;
			request.addParameter('PitanjeTekst', TYPES.NVarChar, PitanjeTekst);
			request.addParameter('PkPitanjeTip', TYPES.Int, PkPitanjeTip);
			request.addParameter('PitanjeAktivnoDaNe', TYPES.Int, PitanjeAktivnoDaNe);
			request.addParameter('PitanjeObaveznoDaNe', TYPES.Int, PitanjeObaveznoDaNe);
			request.addParameter('PkUser', TYPES.Int, user.PkUsera);
			request.addParameter('User', TYPES.NVarChar, user.ImePrezimeUsera);
			request.addOutputParameter('PkPitanje', TYPES.Int, null);
			return makeObservableConnectionWithOutput(request, conn, res, db, 'PkPitanje');
		};

		insertPitanje()
			.pipe(
				first(),
				mergeMap(({ PkPitanje }) => {
					pitanje.PkPitanje = PkPitanje;
					return insertPitanjePredlozak({ PkPitanje, PkPredlozak, db, pitanje, res, user });
				})
			)
			.subscribe(({ PkPredlozakPitanje }) => {
				applyChanges();
			});
	}
});

router.put('/predlozak/proglasiAnketom', async function (req, res) {
	const hasAcces = await hasClaim({ modul: modulName, claim: 'Predlozak' }, req);
	if (!hasAcces) {
		return res.status(401).send({ message: 'STD:UNATHORIZED_ACCES' });
	}
	const user = await handleRequestUser(req, res);

	const { PkPredlozak, AnketaAnonimnaDaNe, AnketaOtvorenaDo, AnketaOtvorenaOd, Clanstva, AnketaNaziv } = req.body;

	const convertToAnketa = () => {
		const { request, conn } = constructConnection('Ankete.spPredlozakProglasiAnketom', res.locals.currDatabase, res, db);
		request.addParameter('PkPredlozak', TYPES.Int, PkPredlozak);
		request.addParameter('PkUser', TYPES.Int, user.PkUsera);
		request.addParameter('User', TYPES.NVarChar, user.ImePrezimeUsera);
		request.addParameter('AnketaNaziv', TYPES.NVarChar, AnketaNaziv);
		request.addParameter('AnketaAnonimnaDaNe', TYPES.Int, AnketaAnonimnaDaNe);
		request.addParameter('AnketaOtvorenaDo', TYPES.Date, AnketaOtvorenaDo);
		request.addParameter('AnketaOtvorenaOd', TYPES.Date, AnketaOtvorenaOd);
		const vidljivoClanovi = Clanstva ? Clanstva.join(',') : null;
		vidljivoClanovi && request.addParameter('VidljivaPkVrstiClanovimaArray', TYPES.NVarChar, vidljivoClanovi);
		request.addOutputParameter('PkAnketa', TYPES.Int, null);
		return makeObservableConnectionWithOutput(request, conn, res, db, 'PkAnketa');
	};

	const mapUsersToAnketa = ({ PkAnketa, PkUser }) => {
		const { request, conn } = constructConnection('Ankete.spUserMapToAnketa', res.locals.currDatabase, res, db);
		request.addParameter('PkAnketa', TYPES.Int, PkAnketa);
		request.addParameter('PkUser', TYPES.Int, tryDecryptOrReturnInput(PkUser));
		request.addParameter('PkUserPromjena', TYPES.Int, user.PkUsera);
		request.addParameter('UserPromjena', TYPES.NVarChar, user.ImePrezimeUsera);
		return makeObservableConnection(request, conn, res, db);
	};

	const fetchUsersForEmailSend = ({ PkAnketa, PkVrstaClanstva }) => {
		const { request, conn } = constructConnection('Ankete.spUsersForAnketaSend', res.locals.currDatabase, res, db);

		request.addParameter('PkAnketa', TYPES.Int, PkAnketa);
		request.addParameter('PkClanstvo', TYPES.Int, PkVrstaClanstva);

		return makeObservableConnection(request, conn, res, db);
	};

	convertToAnketa()
		.pipe(first())
		.subscribe(({ PkAnketa }) => {
			return forkJoin(Clanstva.map((PkVrstaClanstva) => fetchUsersForEmailSend({ PkAnketa, PkVrstaClanstva })))
				.pipe(first())
				.subscribe((clanstva) => {
					if (clanstva) {
						const sendEmails = (u) => {
							return from(sendAnketaLinkMail({ user: u, lang: res.locals.language, PkAnketa: PkAnketa }));
						};
						const users = flatten(clanstva);

						forkJoin(flatten([...users.map((u) => [sendEmails(u), mapUsersToAnketa({ PkAnketa, PkUser: u.PkUsera })])]))
							.pipe(first())
							.subscribe({
								next: (onSend) => {
									res.send(onSend);
								},
								error: (err) => {
									res.status(500).send(err);
								}
							});
					}
				});
		});
});

router.put('/anketa/manage', async function (req, res) {
	const hasAcces = await hasClaim({ modul: modulName, claim: 'Anketa' }, req);
	if (!hasAcces) {
		return res.status(401).send({ message: 'STD:UNATHORIZED_ACCES' });
	}
	const user = await handleRequestUser(req, res);

	const { PkAnketa, IsActive, AnketaOtvorenaOd, AnketaOtvorenaDo } = req.body;

	const manageAnketa = () => {
		const { request, conn } = constructConnection('Ankete.spAnketa_Update', res.locals.currDatabase, res, db);

		request.addParameter('PkAnketa', TYPES.Int, PkAnketa);
		request.addParameter('IsActive', TYPES.Int, IsActive);
		request.addParameter('AnketaOtvorenaOd', TYPES.DateTime, AnketaOtvorenaOd);
		request.addParameter('AnketaOtvorenaDo', TYPES.DateTime, AnketaOtvorenaDo);
		request.addParameter('PkUser', TYPES.Int, user.PkUsera);
		request.addParameter('User', TYPES.NVarChar, user.ImePrezimeUsera);
		return makeObservableConnection(request, conn, res, db);
	};

	manageAnketa()
		.pipe(first())
		.subscribe((done) => {
			res.send({ message: 'OK' });
		});
});

