const nodemailer = require("nodemailer");
const Handlebars = require("handlebars");
const fs = require("fs");
const path = require("path");
const templateText = {
	hr: require("./i18n/hr"),
	en: require("./i18n/en"),
};

function mailInit() {
	// Autentifikacijski podaci za slanje maila
	const { incomingMailParams: mailConfig } = global.appConfig.mailConfig;

	let transportOptions = {
		auth: {
			user: mailConfig.mailUser,
			pass: mailConfig.mailPass,
		},
	};

	// Postavljanje konfiguracije za slanje maila
	if (mailConfig.mailServiceDaNe === "true") {
		transportOptions.service = mailConfig.mailService;
	} else {
		transportOptions.host = mailConfig.mailHost;
		transportOptions.port = mailConfig.mailPort;
		transportOptions.secure = false;
		// do not fail on invalid certs
		transportOptions.tls = { rejectUnauthorized: false };
	}
	// Kreiranje transporta za slanje maila
	return nodemailer.createTransport(transportOptions);
}

/**
 *
 * @param {string} lang hr|en
 * @param {string} key key from dictionary
 * @returns string
 */
function translate(lang, key) {
	return templateText[lang][key];
}

function constructEmailOptions({lang, dest: { to, subject }, templateParams, templateUrl, attachments }) {
	const { mailConfig } = global.appConfig;
	const source = fs.readFileSync(path.join(templateUrl), "utf8");
	const template = Handlebars.compile(source);
	return {
		from: `${translate(lang,'ORGANIZATION')} <${mailConfig.incomingMailParams.mailUser}>`,
		to: to,
		subject: `Alumni | ${subject}`, //'MyApplication | Password Reset',
		html: template(templateParams),
		attachments: attachments
	};
}

function sendMail({mailTransport, options}) {
	return new Promise((resolve, reject) => {
		mailTransport.sendMail(options, (err, info) => {
			if (err) return reject(err);
			return resolve(info);
		});
	});
}

module.exports = {
	mailInit,
    translate,
    constructEmailOptions,
    sendMail
};
