const { google } = require("googleapis");
const axios = require("axios").default;
const router = require("express").Router();

const { google: googleCfg } = global.appConfig.oauth;
const oauth2Client = new google.auth.OAuth2(
	googleCfg.GOOGLE_CLIENT_ID,
	googleCfg.GOOGLE_CLIENT_SECRET,
	/*
	 * This is where Google will redirect the user after they
	 * give permission to your application
	 */
	googleCfg.redirectUrl
);

function getGoogleAuthURL() {
	/*
	 * Generate a url that asks permissions to the user's email and profile
	 */
	const scopes = ["https://www.googleapis.com/auth/userinfo.profile", "https://www.googleapis.com/auth/userinfo.email"];

	return oauth2Client.generateAuthUrl({
		access_type: "offline",
		prompt: "consent",
		scope: scopes, // If you only need one scope you can pass it as string
	});
}

async function getGoogleUser({ code }) {
	const { tokens } = await oauth2Client.getToken(code);
	// Fetch the user's profile with the access token and bearer
	const googleUser = await axios.get(`https://www.googleapis.com/oauth2/v1/userinfo?alt=json&access_token=${tokens.access_token}`, {
			headers: {
				Authorization: `Bearer ${tokens.id_token}`,
			},
		})
		.then(res => res.data)
		.catch(error => {
			throw new Error(error.message);
		});

	return googleUser;
}

router.get('/oauth/complete', async (req, res) => {
	try {
		const user = await getGoogleUser(req.query);
		res.send(user);
			
	} catch (error) {
		res.status(500).send({...error , message: "STD:GOOGLE_AUTH_ERROR"});		
	}
});
router.get("/oauth", async (req, res) => {
	res.send({url:getGoogleAuthURL()});
});
module.exports = {
	getGoogleUser,
	router,
};
