// select
router.get('/obavijest', function (req, res) {
	const conn = db.createConnection(res.locals.currDatabase);
	const request = db.createRequest('[Alumni].spObavijest_Select', conn, res);
	request.addParameter('PkObavijest', TYPES.Int, req.query.PkObavijest);

	db.execStoredProcFromNode(request, conn, res, (output, outputParams, result) => simpleDbResolve(output, outputParams, result, res));
});

router.get('/obavijestCategoryGroup', async function (req, res, next) {
	const user = await handleRequestUser(req, res);
	try {
		const conn = db.createConnection(res.locals.currDatabase);
		const request = db.createRequest('Alumni.[spObavijestKategorijaJoined_Select]', conn, res);
		request.addParameter('skip', TYPES.Int, req.query.skip);
		request.addParameter('take', TYPES.Int, req.query.take);
		request.addParameter('PkViewUsera', TYPES.Int, decryptIfEncrypted(user.PkUsera));
		request.addParameter('PkVrstaClanstva', TYPES.Int, exists(user.PkVrstaClanstva) ? user.PkVrstaClanstva : PkVrstaClanstvaForAnonymous);

		db.execStoredProcFromNode(request, conn, res, (output, outputParams, result) => {
			simpleDbResolve(output, outputParams, result, res);
		});
	} catch (error) {
		next(error);
	}
});

router.get('/obavijestForCategoryFTS', async function (req, res, next) {
	const user = await handleRequestUser(req, res);
	try {
		const conn = db.createConnection(res.locals.currDatabase);
		const request = db.createRequest('Alumni.[spObavijest_FTSSelect]', conn, res);
		request.addParameter('skip', TYPES.Int, req.query.skip);
		request.addParameter('take', TYPES.Int, req.query.take);
		request.addParameter('Search', TYPES.NVarChar, req.query.search);
		request.addParameter('PkObavijestKategorija', TYPES.NVarChar, req.query.PkObavijestKategorija);
		request.addParameter('PkUsera', TYPES.Int, exists(req.query.PkUsera) ? req.query.PkUsera : null);
		request.addParameter('PkViewUsera', TYPES.Int, decryptIfEncrypted(user.PkUsera));
		//Ukoliko PkUsera postoji to znaci da smo usli u moje obavijesti, stoga ne saljemo PkVrstaClanstva
		if (!req.query.PkUsera) {
			request.addParameter('PkVrstaClanstva', TYPES.Int, exists(user.PkVrstaClanstva) ? user.PkVrstaClanstva : PkVrstaClanstvaForAnonymous);
		}

		db.execStoredProcFromNode(request, conn, res, (output, outputParams, result) => {
			simpleDbResolve(output, outputParams, result, res);
		});
	} catch (error) {
		if (global.appConfig.ukljuciConsoleLog.ukljuciStackTrace) console.trace('obavijestForCategoryFTS', error);
		next(error);
	}
});

