// insert
router.post('/obavijest', async function (req, res) {
	const hasAcces = await hasClaim({ modul: modulName, claim: 'Dodavanje' }, req);

	if (hasAcces) {
		const user = await handleRequestUser(req, res);

		const conn = db.createConnection(res.locals.currDatabase);
		const request = db.createRequest('[Alumni].spObavijest_Insert', conn, res);

		request.addParameter('Naslov', TYPES.NVarChar, req.body.Naslov);
		request.addParameter('CoverImagePath', TYPES.NVarChar, req.body.CoverImagePath);
		request.addParameter('Html', TYPES.NVarChar, req.body.Html);
		request.addParameter('DatumObjave', TYPES.Date, req.body.DatumObjave);
		req.body.DatumIskljucenjaObavijest && request.addParameter('DatumIskljucenjaObavijest', TYPES.Date, req.body.DatumIskljucenjaObavijest);
		request.addParameter('UserUnos', TYPES.NVarChar, req.body.UserUnos ? req.body.UserUnos : user.ImePrezimeUsera);
		request.addParameter('UserZadnjePromjene', TYPES.NVarChar, req.body.UserZadnjePromjene ? req.body.UserZadnjePromjene : user.ImePrezimeUsera);
		request.addParameter('Status', TYPES.Int, req.body.Status);
		request.addParameter('Tags', TYPES.NVarChar, req.body.Tags);
		request.addParameter('PkUsera', TYPES.Int, req.body.PkUsera ? req.body.PkUsera : user.PkUsera);
		request.addParameter('PkObavijestKategorija', TYPES.Int, req.body.PkObavijestKategorija);

		request.addOutputParameter('PkObavijest', TYPES.Int, null);

		db.execStoredProcFromNode(request, conn, res, (output, outputParams, result) => {
			if (output == 'OK') {
				const { PkObavijest } = outputParams;
				res.status(200).send({ PkObavijest });
			} else {
				res.status(500).send({ message: 'STD:INVALID_REQUEST' });
			}
		});
	} else {
		res.status(401).send({ message: 'STD:UNATHORIZED_ACCES' });
	}
});

