// delete
router.delete('/obavijest', async function (req, res) {
	const hasAcces = await hasClaim({ modul: modulName, claim: 'Dodavanje' }, req);

	if (hasAcces) {
		const conn = db.createConnection(res.locals.currDatabase);
		const request = db.createRequest('[Alumni].spObavijest_Delete', conn, res);

		try {
			const savePath = constructDirDepth(['public', 'obavijesti', req.query.PkObavijest + '/']);
			const files = fs.readdirSync(savePath);
			for (const file of files) {
				fs.unlinkSync(path.join(savePath, file));
			}
			fs.rmdirSync(savePath);

			request.addParameter('PkObavijest', TYPES.Int, req.query.PkObavijest);
			db.execStoredProcFromNode(request, conn, res, (output, outputParams, result) => simpleDbResolve(output, outputParams, result, res));
		} catch (error) {
			res.status(500).send({ message: 'STD:INVALID_REQUEST' });
		}
	} else {
		res.status(401).send({ message: 'STD:UNATHORIZED_ACCES' });
	}

});

