//Slanje maila
router.post('/sendObavijestLinkMail', function (req, res) {
	const { PkObavijest, Clanstva } = req.body;

	const fetchUsersForEmailSend = ({ PkVrstaClanstva }) => {
		const { request, conn } = constructConnection('Alumni.spUsersForObavijestSend', res.locals.currDatabase, res, db);

		request.addParameter('PkClanstvo', TYPES.Int, PkVrstaClanstva);

		return makeObservableConnection(request, conn, res, db);
	};

	forkJoin(Clanstva.map((PkVrstaClanstva) => fetchUsersForEmailSend({ PkVrstaClanstva })))
		.pipe(first())
		.subscribe((clanstva) => {
			if (clanstva) {
				const sendEmails = (u) => {
					return from(sendObavijestLinkMail({ user: u, lang: res.locals.language, PkObavijest: PkObavijest }));
				};

				const users = flatten(clanstva);

				forkJoin(users.map((u) => sendEmails(u)))
					.pipe(first())
					.subscribe({
						next: (onSend) => {
							res.send(onSend);
						},
						error: (err) => {
							global.appConfig.ukljuciConsoleLog.ukljuciStackTrace && console.trace(err);

							res.status(500).send(err);
						}
					});
			} else {
				res.send();
			}
		});
});

