const fs = require('fs');

function pripremiFoldere() {
	fs.exists(global.appConfig.directoryParams.uploadPath, function (exists) {
		if (exists) {
		} else {
			fs.mkdir(global.appConfig.directoryParams.uploadPath, function (err) {
				if (err) {
					global.systemLogger.log({
						level: 'error',
						message: 'tools.pripremiFoldere(): Error in  uploadPath folder creation '
					});
				}
			});
		}
	});
	fs.exists(global.appConfig.directoryParams.uploadPathTest, function (exists) {
		if (exists) {
		} else {
			fs.mkdir(global.appConfig.directoryParams.uploadPathTest, function (err) {
				if (err) {
					// logger.log('error', 'tools.pripremiFoldere(): Error in uploadPath folder creation');
				}
			});
		}
	});
	fs.exists(global.appConfig.directoryParams.nodeSrv + 'logs', function (exists) {
		if (exists) {
		} else {
			fs.mkdir(global.appConfig.directoryParams.nodeSrv + 'logs', function (err) {
				if (err) {
					//  logger.log('error', 'tools.pripremiFoldere(): Error in logs folder creation');
				}
			});
		}
	});
}

function checkFileExists(filePath) {
	try {
		fs.statSync(filePath);
		return true;
	} catch (e) {
		return false;
	}
}

function checkFolderExists(filePath) {
	try {
		fs.statSync(filePath);
		return true;
	} catch (e) {
		return false;
	}
}
