const path = require("path");
const { mailInit, constructEmailOptions, sendMail, translate } = require("../mailService");


async function sendPassResetEmail(user, lang) {
    const { passReset } = global.appConfig.mailConfig.emails;
    const { mailConfig } = global.appConfig;

    const mailTransport = mailInit();
    const options = constructEmailOptions({
        lang: lang,
        dest: {
            to: user.Email,
            subject: translate(lang, "RESET_PASSWORD"),
        },
        templateParams: {
            RESET_PASSWORD: translate(lang, "RESET_PASSWORD"),
            RESET_PASSWORD_HEADER: translate(lang, "RESET_PASSWORD_HEADER"),
            POSTOVANI: translate(lang, "POSTOVANI"),
            ime: user.ImeUsera,
            prezime: user.PrezimeUsera,
            RESET_PASSWORD_TEXT_0: translate(lang, "RESET_PASSWORD_TEXT_0"),
            link: `${passReset.url}/${user.UUID}`,
            RESET_PASSWORD_BTN_LABEL: translate(lang, 'RESET_PASSWORD_BTN_LABEL'),
            ORG_NEED_HELP: translate(lang, 'ORG_NEED_HELP'),
            RESET_PASSWORD_BACKUP_LINK: translate(lang, 'RESET_PASSWORD_BACKUP_LINK'),
            organizationContactLink: mailConfig.incomingMailParams.organizationContactLink,
            staticUrl: mailConfig.incomingMailParams.staticUrl,
            ORGANIZATION: translate(lang, "ORGANIZATION"),
            ADDRESS: translate(lang, "ADDRESS"),
            appLink: mailConfig.incomingMailParams.appLink,
        },
        attachments: passReset.attachments,
        templateUrl: path.join(passReset.template)
    });

    return await sendMail({ mailTransport, options });
}

module.exports = { sendPassResetEmail };